@use 'base/variables' as v;
@use 'base/mixins' as mix;

.header{
    background-color: v.$verde;
    
    @include mix.tablet {
        &.fijo{
            position: sticky;
            top: 0;
            box-shadow: .1rem .2rem .1rem #717171;
            h1{
                font-size: 3rem;
            }
    
            nav{
    
                a{
                    font-size: 2rem;
                }
            }
        }
    }
    h1{
        color: v.$blanco;
        text-align: center;
        @include mix.tablet {
            margin: 0;
        }
    }
}

.contenido-header{
    padding: 2rem 0;
    @include mix.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.nav-principal{
    padding: 2rem 0;
    display: flex;
    flex-direction: column;
    text-align: center;
    @include mix.tablet {
        flex-direction: row;
        gap: 2rem;
        padding: 0;
    }
    a{
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1rem;
        &:last-child{
            margin-bottom: 0;
        }
        @include mix.tablet {
            margin-bottom: 0;
        }

        &:hover{
            color: v.$amarillo;
        }
    }    
}