* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

img {
  display: block;
  max-width: 100%;
  width: 100%;
  height: auto;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

ul {
  list-style: none;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #000000;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.header {
  background-color: #4CB8B3;
}
@media (min-width: 768px) {
  .header.fijo {
    position: sticky;
    top: 0;
    box-shadow: 0.1rem 0.2rem 0.1rem #717171;
  }
  .header.fijo h1 {
    font-size: 3rem;
  }
  .header.fijo nav a {
    font-size: 2rem;
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}

.contenido-header {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.nav-principal {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 768px) {
  .nav-principal {
    flex-direction: row;
    gap: 2rem;
    padding: 0;
  }
}
.nav-principal a {
  color: #ffffff;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
.nav-principal a:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .nav-principal a {
    margin-bottom: 0;
  }
}
.nav-principal a:hover {
  color: #FEF46D;
}

.video {
  position: relative;
  overflow: hidden;
  max-height: 70rem;
  min-width: 50rem;
}
.video .overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 176, 0.1) 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  display: block;
  width: auto;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
  color: #ffffff;
}
.contenido-video h2 {
  color: #ffffff;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .contenido-video h2 {
    font-size: 7rem;
  }
}
.contenido-video p {
  background-color: #F53756;
  display: inline-block;
  padding: 1rem 2rem;
  color: #ffffff;
  font-weight: 700;
  font-size: 1.6rem;
}

.sobre-festival {
  padding: 5rem 0;
  align-items: center;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  margin-top: 4rem;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: #F53756;
  padding: 2rem 0;
  font-weight: 700;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 4rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}

.escenario {
  padding-top: 2rem;
  margin-bottom: 4rem;
}
@media (min-width: 768px) {
  .escenario {
    margin-bottom: 0;
  }
}
.escenario .nombre-escenario:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
  padding: 1rem;
}

.edm .nombre-escenario:after {
  background-image: url("../img/icono_edm.png");
}

.rock .nombre-escenario:after {
  background-image: url("../img/icono_rock.png");
}

.bg-amarillo {
  background-color: #FEF46D;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #fef03a;
}

.bg-verde {
  background-color: #4CB8B3;
}
.bg-verde li:nth-child(odd) {
  background-color: #3b9691;
}

.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
  margin-bottom: 5rem;
}

.galeria-imagenes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    grid-template-columns: repeat(4, 1fr);
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  width: 80%;
}

.btn-cerrar {
  background-color: white;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  color: #000000;
  display: grid;
  place-items: center;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #ffffff;
}
.boletos .contenedor-boletos {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  color: black;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  color: #ffffff;
  text-align: center;
}
.pase-header {
  background-color: #F53756;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #ffffff;
}
.pase-body ul li, .pase-body ul :nth-child(odd) {
  padding: 1rem;
  background-color: #ffffff;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #752f97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.tipo-pase {
  text-align: center;
  padding: 1rem 0;
  color: #ffffff;
  font-size: 2.4rem;
}

.site-footer {
  background-color: #F53756;
}
.site-footer p {
  color: #ffffff;
  text-align: center;
  padding: 1rem 0;
}