@use 'base/variables' as v;
@use 'base/mixins' as mix;

.lineup{
    background-color: v.$rosa;
    padding: 5rem 0;
    h3, p{
        text-align: center;
        color: v.$blanco;
    }

    .dia{
        text-transform: uppercase;
        margin: 4rem 0;
    }
}

.escenarios-contenedor{
    @include mix.tablet {
        display:grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 5rem;
    }

    p{
        color: v.$negro;
    }
}

.escenario{
    padding-top: 2rem;
    margin-bottom: 4rem;

    @include mix.tablet {
        margin-bottom: 0;
    }

    .nombre-escenario{

        &:after{
            content: "";
            display: block;
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 0 auto;
            padding: 1rem;
        }
    }
}

.edm .nombre-escenario:after{
    background-image: url('../img/icono_edm.png');
}

.rock .nombre-escenario:after{
    background-image: url('../img/icono_rock.png');
}

.bg-amarillo{
    background-color: v.$amarillo;

    li:nth-child(odd){
        background-color: darken(v.$amarillo, 10%);
    }
}

.bg-verde{
    background-color: v.$verde;

    li:nth-child(odd){
        background-color: darken(v.$verde, 10%);
    }
}

.calendario{
    li{
        padding: 2.5rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}