@use 'variables' as v;

*{
    margin: 0;
    padding: 0;
}

html{
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

*,*:before,*:after{
    box-sizing: inherit;
}

body{
    font-family: v.$fuente_principal;
    font-size: 1.6rem;

    &.fijar-body{
        overflow: hidden;
    }
}

img{
    display: block;
    max-width: 100%;
    width: 100%;
    height: auto;
}

p{
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}

ul{
    list-style: none;
}

.contenedor{
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
    color: v.$negro;
}


h1{
    font-size: 4rem;
}
h2{
    font-size: 3.6rem;
}
h3{
    font-size: 3rem;
}
