@use 'base/variables' as v;
@use 'base/mixins' as mix;

.sobre-festival{
    padding: 5rem 0;
    align-items: center;
    @include mix.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 5rem;
    }
}


.contenido-festival{
    margin-top: 4rem;
    text-align: center;

    @include mix.tablet {
        margin-top: 0;
    }
    
    h2{
        margin-bottom: 2rem;
    }

    .fecha{
        color: v.$rosa;
        padding: 2rem 0;
        font-weight: 700;
    }
}