@use 'base/variables' as v;
@use 'base/mixins' as mix;

.galeria{
    padding: 5rem 0;
    h3{
        text-align: center;
        margin-bottom: 5rem;
    }
}

.galeria-imagenes{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;

    @include mix.tablet {
        grid-template-columns: repeat(3,1fr);
    }
    
    @include mix.desktop {
        grid-template-columns: repeat(4,1fr);
    }
}

.overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / .5);

    picture{
        display: block;
        width: 80%;
    }
}

.btn-cerrar{
    background-color: white;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    color: v.$negro;
    display: grid;
    place-items: center;
    &:hover{
        cursor: pointer;
    }
}
