@use 'base/variables' as v;
@use 'base/mixins' as mix;

.video{
    position: relative;
    overflow: hidden;
    max-height: 70rem;
    min-width: 50rem;
    .overlay{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(270deg, rgba(165,3,255,0.5) 0%, rgba(255,0,176,0.1) 100%);
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    video{
        display: block;
        width: auto;

        @include mix.desktop {
        width: 100%;
        }

    }
}

.contenido-video{
    // text-align: center;
    z-index: 1;
    color: v.$blanco;
    h2{
        color: v.$blanco;
        font-size: 3rem;
        @include mix.tablet {
            font-size: 7rem;
        }
    }

    p{
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 1.6rem;
    }
}