@use 'base/variables' as v;
@use 'base/mixins' as mix;

.boletos{
    padding: 5rem 0;
    h3{
        text-align: center;
        color: v.$blanco;
    }

    .contenedor-boletos{
        padding: 5rem 0;
        @include mix.tablet {
            display:grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 5rem;
        }
    }
}

.pase{
    color: black;

    &-header,
    &-footer{
        padding: .5rem;
        p{
            color: v.$blanco;
            text-align: center;
        }
    }

    &-header{
        background-color: v.$rosa;
        p{
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }

    &-body{
        background-color: v.$blanco;

        ul{
            li, :nth-child(odd){
                padding: 1rem;
                background-color: v.$blanco;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }

    &-footer{
        background-color: v.$morado;
        p{
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
    
}

.tipo-pase{
    text-align: center;
    padding: 1rem 0;
    color: v.$blanco;
    font-size: 2.4rem;
}